.tu-dashboard {
	display: flex;
	flex-direction: column;
	gap: calc(var(--default-grid-baseline) * 4);
	padding: calc(var(--default-grid-baseline) * 2) 0;
	width: 100%;
	box-sizing: border-box;
}
.tu-dashboard__hero {
	position: relative;
	overflow: hidden;
	border-radius: var(--border-radius-large);
	background-color: var(--color-background-muted);
	padding: calc(var(--default-grid-baseline) * 6);
	color: var(--color-primary-text);
	width: 100%;
	box-sizing: border-box;
}
.tu-dashboard__hero--with-background {
	background-image: var(--tu-dashboard-hero-image);
	background-position: center;
	background-size: cover;
}
.tu-dashboard__hero::after {
	content: '';
	position: absolute;
	inset: 0;
	background: linear-gradient(135deg, rgba(0, 0, 0, 0.45), rgba(0, 0, 0, 0.1));
	pointer-events: none;
	z-index: 0;
}
.tu-dashboard__hero-body {
	position: relative;
	z-index: 1;
	display: grid;
	gap: calc(var(--default-grid-baseline) * 3);
	max-width: 520px;
	margin: 0 auto;
}
.tu-dashboard__welcome {
	margin: 0;
	font-size: 1.75rem;
	font-weight: 600;
	color: var(--color-primary-text);
}
.tu-dashboard__form {
	display: grid;
	gap: calc(var(--default-grid-baseline) * 2);
	width: 100%;
}
.tu-dashboard__form-row {
	display: flex;
	flex-wrap: wrap;
	gap: calc(var(--default-grid-baseline) * 1.5);
	align-items: center;
	width: 100%;
}
.tu-dashboard__select-wrapper {
	flex: 1 1 100%;
	min-width: 0;
	min-width: 0;
}
.tu-dashboard__select-wrapper .nc-select {
	width: 100%;
}
.tag-mode-toggle {
	display: inline-flex;
	align-items: center;
	gap: calc(var(--default-grid-baseline));
	padding: 8px 12px;
	border-radius: var(--border-radius);
	border: 1px solid var(--color-border);
	background-color: var(--color-main-background);
	cursor: pointer;
	user-select: none;
	transition: background-color var(--animation-quick) ease, border-color var(--animation-quick) ease;
	min-height: 46px;
	min-width: 160px;
	justify-content: center;
}
.tag-mode-toggle:hover {
	background-color: var(--color-background-hover);
}
.tag-mode-toggle:focus-visible {
	outline: 2px solid var(--color-primary-element);
	outline-offset: 2px;
}
.tag-mode-toggle:active {
	background-color: var(--color-background-muted);
}
.tag-mode-toggle .toggle-label {
	font-size: 0.8rem;
	font-weight: 600;
	color: var(--color-text-maxcontrast);
	letter-spacing: 0.04em;
}
.tag-mode-toggle .toggle-label.is-active {
	color: var(--color-primary-element);
}
.tag-mode-toggle .toggle-icon {
	display: inline-flex;
	align-items: center;
	justify-content: center;
	width: 28px;
	height: 28px;
}
.tag-mode-toggle .toggle-icon svg {
	width: 22px;
	height: 22px;
	fill: var(--color-primary-element);
}
.tu-dashboard__tag-mode {
	flex: 0 0 150px;
	align-self: stretch;
	background: none;
	box-shadow: none;
}
.tu-dashboard__submit {
	align-self: flex-start;
	min-width: 160px;
}
.tu-dashboard__content {
	display: flex;
	flex-wrap: wrap;
	gap: calc(var(--default-grid-baseline) * 3);
	width: 100%;
	align-items: stretch;
	padding: 0 calc(var(--default-grid-baseline) * 2);
	box-sizing: border-box;
}
.tu-dashboard__content--full {
	flex-direction: column;
}
.tu-dashboard__favorites {
    flex: 0 0 30%;
    max-width: 360px;
    background-color: transparent;
    border-radius: var(--border-radius);
    padding: calc(var(--default-grid-baseline) * 2);
    min-width: 280px;
}
.tu-dashboard__results {
	flex: 1 1 auto;
	display: flex;
	flex-direction: column;
	gap: calc(var(--default-grid-baseline) * 2);
	min-width: 320px;
}
.tu-dashboard__results-header {
	display: flex;
	align-items: center;
	justify-content: space-between;
	gap: calc(var(--default-grid-baseline) * 2);
}
.tu-dashboard__section-title {
	margin: 0;
	font-size: 1.25rem;
	font-weight: 600;
}
.tu-dashboard__actions {
	display: flex;
	gap: calc(var(--default-grid-baseline) * 1.5);
}
.tu-dashboard__action {
	padding: 0;
}
.tu-dashboard__list {
	background-color: var(--color-background-default);
	border-radius: var(--border-radius);
	border: 1px solid var(--color-border);
	box-shadow: var(--box-shadow-level-1);
	padding: calc(var(--default-grid-baseline) * 2);
}
.tu-dashboard__list--favorites {
	background-color: transparent;
	border: none;
	box-shadow: none;
	padding: 0;
}
@media (max-width: 768px) {
.tu-dashboard__hero {
		padding: calc(var(--default-grid-baseline) * 4);
}
.tu-dashboard__form-row {
		flex-direction: column;
		align-items: stretch;
}
.tu-dashboard__actions {
		justify-content: flex-end;
}
}

