___CSS_LOADER_EXPORT___.push([module.id, `
.file-list {
	display: flex;
	flex-direction: column;
	gap: calc(var(--default-grid-baseline) * 2);
}
.file-list__state {
	display: inline-flex;
	align-items: center;
	gap: calc(var(--default-grid-baseline) * 2);
	color: var(--color-text-maxcontrast);
}
.file-list__state p {
	margin: 0;
}
.file-table {
	width: 100%;
	border-collapse: collapse;
	border-spacing: 0;
	font-size: 0.95rem;
	table-layout: fixed;
}
.file-table__head {
	text-align: left;
	padding: 12px 16px;
	font-weight: 600;
	color: var(--color-text-maxcontrast);
	background-color: var(--color-background-hover);
}
.file-table__head--name {
	width: 40%;
}
.file-table__head--size {
	width: 10%;
	text-align: right;
}
.file-table__row {
    border-bottom: 1px solid var(--color-border);
    background: var(--color-background-default);
    min-height: 112px;
}
.file-table__row:hover {
	background-color: var(--color-background-hover);
}
.file-table__cell {
	padding: 14px 16px;
	vertical-align: middle;
	color: var(--color-main-text);
}
.file-table__cell--name {
	padding-left: 20px;
}
.file-table__cell--numeric {
	text-align: right;
}
.file-table__link {
	display: inline-flex;
	align-items: center;
	gap: calc(var(--default-grid-baseline) * 1.5);
	color: inherit;
	text-decoration: none;
	width: 100%;
}
.file-table__link:hover .file-table__title {
	text-decoration: underline;
}
.file-table__thumb {
	width: 96px;
	height: 96px;
	display: inline-flex;
	align-items: center;
	justify-content: center;
	border-radius: var(--border-radius);
	background-color: var(--color-background-dark);
	overflow: hidden;
}
.file-table__thumb img {
	width: 100%;
	height: 100%;
	object-fit: cover;
}
.file-table__title {
	font-weight: 600;
	white-space: nowrap;
	text-overflow: ellipsis;
	overflow: hidden;
	max-width: 420px;
	display: inline-block;
}
.file-list__grid {
	display: grid;
	gap: calc(var(--default-grid-baseline) * 2);
	grid-template-columns: repeat(auto-fit, minmax(220px, 1fr));
}
.file-card {
	background-color: var(--color-background-default);
	border: 1px solid var(--color-border);
	border-radius: var(--border-radius);
	box-shadow: var(--box-shadow-level-1);
	transition: transform var(--animation-quick) ease, border-color var(--animation-quick) ease;
}
.file-card__button {
	all: unset;
	display: flex;
	flex-direction: column;
	gap: calc(var(--default-grid-baseline) * 1.5);
	padding: calc(var(--default-grid-baseline) * 2);
	width: 100%;
	height: 100%;
	cursor: pointer;
	border-radius: inherit;
}
.file-card__button:focus-visible {
	outline: 2px solid var(--color-primary-element);
	outline-offset: 2px;
}
.file-card:hover,
.file-card:focus-within {
	transform: translateY(-2px);
	border-color: var(--color-primary-element);
}
.file-card:hover .file-card__meta,
.file-card:focus-within .file-card__meta,
.file-card:hover .file-card__description,
.file-card:focus-within .file-card__description {
	opacity: 1;
	max-height: 240px;
}
.file-card__thumb {
	width: 100%;
	height: 220px;
	display: flex;
	align-items: center;
	justify-content: center;
	border-radius: var(--border-radius);
	background-color: var(--color-background-dark);
	overflow: hidden;
}
.file-card__thumb img {
	width: 100%;
	height: 100%;
	object-fit: cover;
}
.file-card__body {
	display: flex;
	flex-direction: column;
	gap: calc(var(--default-grid-baseline) * 1.25);
}
.file-card__name {
	margin: 0;
	font-size: 1rem;
	font-weight: 600;
}
.file-card__meta {
	display: grid;
	gap: calc(var(--default-grid-baseline) * 0.75);
	margin: 0;
	opacity: 0;
	max-height: 0;
	overflow: hidden;
	transition: opacity var(--animation-quick) ease, max-height var(--animation-quick) ease;
}
.file-card__meta-entry {
	display: grid;
	grid-template-columns: auto 1fr;
	gap: calc(var(--default-grid-baseline) * 0.5);
}
.file-card__meta-label {
	margin: 0;
	font-weight: 600;
	color: var(--color-text-maxcontrast);
}
.file-card__meta-value {
	margin: 0;
	color: var(--color-main-text);
	word-break: break-word;
}
.file-card__description {
	margin: 0;
	color: var(--color-text-maxcontrast);
	opacity: 0;
	max-height: 0;
	overflow: hidden;
	transition: opacity var(--animation-quick) ease, max-height var(--animation-quick) ease;
}
.file-list__state + .file-table {
	margin-top: calc(var(--default-grid-baseline) * 2);
}
@media (max-width: 768px) {
.file-table__head,
	.file-table__cell {
		padding: 12px;
}
.file-table__title {
		max-width: 180px;
}
.file-list__grid {
		grid-template-columns: repeat(auto-fit, minmax(180px, 1fr));
}
}
